body {
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: grayscale;
    font-family: "Times New Roman", Times, serif;
  }
  #menu_space {
    margin-bottom: 4em;
  }

  #home_segment {
    background-image: url("./images/home.png"), url("./images/home_bg.png");
    background-color:  #4c809a;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    background-position: center;
  }

  #faculty_segment {
    background-image: url('./images/faculty.png'), url("./images/faculty_bg.png");
    background-color:  #4c809a;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    background-position: center;
  }

  #admission_segment {
    background-image: url('./images/admission.PNG'), url('./images//admission_bg.png');
    background-color:  #4c809a;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    background-position: center;
  }

  #academic_segment {
    background-image: url('./images/academic.PNG'), url('./images/academic_bg.png');
    background-color:  #4c809a;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    background-position: center;
  }

  #contact_segment {
    background-image: url('./images/contact.png'), url('./images/contact_bg.png');
    background-color:  #4c809a;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    background-position: center;
  }

  #student_segment {
    background-image: url('./images/student.png'), url('./images/student_bg.png');
    background-color:  #4c809a;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    background-position: center;
  }

  #form_segment {
    background-image: url('./images/form.png'), url('./images/form_bg.png');
    background-color:  #4c809a;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    background-position: center;
  }

  #faculty_card {
    background-image: linear-gradient(rgba(255,255,255,0.66), rgba(255,255,255,0.66)), url('./images/Faculty_icon/Bg.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
  }
  @media screen and (min-width: 800px) {
    #back_to_top {
      position: fixed;
      cursor: pointer;
      bottom: 2em;
      right: 2em;
    }
    #text_back_to_top {
      visibility: hidden;
    }
  }
  @media(max-width:900px) {
    #back_to_top {
      visibility: hidden;
    }
    #text_back_to_top {
      visibility: visible;
    }
  }
  
  .ui.vertical.segment:not(.inverted) {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
  
  .six.wide.column img {
    width: 100%;
  }
  
  .ui.borderless.inverted.menu {
    background-color: #2f2f2f;
    border-radius: 4px;
    flex-wrap: wrap;
  }
  
  .ui.borderless.inverted.menu .active.item {
    background-color: black;
  }
  
  span.sub {
    color: rgba(0, 0, 0, 0.6);
  }
  
  .ten.wide.column h1 {
    font-size: 2rem;
  }
  
  .ten.wide.column p {
    font-size: 1.5rem;
  }
  
  p code {
    color: #c7254e;
    background-color: #f9f2f4;
    border-radius: 4px;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  
  .ui.tablet.computer.only.padded.grid {
    position: absolute;
    z-index: 8;
    width: 100%;
    top: 1rem;
  }
  
  .ui.mobile.only.grid .ui.menu .ui.vertical.menu {
    display: none;
  }
  
  .ui.mobile.only.grid .ui.vertical.menu .dropdown.icon {
    float: unset;
  }
  
  .ui.mobile.only.grid .ui.vertical.menu .dropdown.icon:before {
    content: "\f0d7";
  }
  
  .ui.mobile.only.grid .ui.vertical.menu .ui.dropdown.item .menu {
    position: static;
    width: 100%;
    background-color: unset;
    border: none;
  }
  
  .ui.mobile.only.grid .ui.vertical.menu .ui.dropdown.item {
    background-color: rgb(47, 47, 47);
  }
  
  .ui.mobile.only.grid .ui.vertical.menu .ui.dropdown.item .menu {
    margin-top: 6px;
  }
  
  .ui.mobile.only.grid .ui.vertical.menu .ui.dropdown.item .menu .header,
  .ui.mobile.only.grid .ui.vertical.menu .ui.dropdown.item .menu .item {
    color: rgba(255, 255, 255, 0.9) !important;
  }
  
  .slide .ui.inverted.vertical.segment {
    padding-top: 16rem;
    padding-bottom: 4rem;
    height: 600px;
  }
  
  .slide .ui.text.container {
    max-width: 70%;
  }
  
  .slick-dotted.slick-slider {
    margin-bottom: 0;
  }
  
  @media only screen and (max-width: 767px) {
    .slick-prev.slick-arrow {
      padding-left: 4rem;
      padding-right: calc(4rem + 20px);
    }
  
    .slick-next.slick-arrow {
      padding-left: 4rem;
      padding-right: calc(4rem + 20px);
    }
  }
  
  .slick-dots {
    bottom: 15px;
  }
  
  .slide .ui.segment h1 {
    font-size: 3rem;
  }
  
  .slide .ui.segment p {
    font-size: 1.5rem;
  }
  .ui.divider {
    padding-top: 3rem;
  }
  .ui.divider h1 {
    font-size: 3rem;
  }

  p {
    font-size: 1.5rem;
  }
  